import React from 'react';
import s from './Dialogs.module.scss';
import DialogItem from './DialogItem';
import Message from './Message';
import { reset, Field, reduxForm } from "redux-form";
import Icon from '../common/Icon';

export default props => {
    const {
        id,
        dialogs,
        messages,
        addMessage,
        ...dialogsProps
    } = props;

    return (
        <div className={s.dialogsWrapper}>

            <ul className={s.dialogs}>
                {dialogs.map( i => <DialogItem {...i} {...dialogsProps}  key={i.id}/>)}
            </ul>

            <div className={s.messages}>
                {id === undefined
                    ? (
                        <div className={s.messagesWarning}>
                            <Icon className={s.messagesIcon} icon={'alert'}/>
                            Не выбран собеседник
                        </div>
                    )
                    : [
                        messages.map( i => Message(i) ),
                        <div className={s.new} key={'newMessage'}>
                            <MessageFormRedux onSubmit={data => addMessage(data)}/>
                        </div>
                    ]
                }
            </div>
        </div>
    );
}

const MessageForm = ({handleSubmit}) => {
    return (
        <form onSubmit={handleSubmit} className={s.Form}>
            <div className={s.Row}>
                <Field
                    className={s.Textarea}
                    component={'textarea'}
                    name={'message'}
                    placeholder={'New message'}
                />
            </div>
            <div className={s.Row + ' ' + s.TextRight}>
                <button type={'submit'} className={s.Button}>Отправить</button>
            </div>
        </form>
    )
};

const MessageFormRedux = reduxForm({
    form: 'new-message-form',
    onSubmitSuccess: (result, dispatch) => dispatch(reset('new-message-form'))
})(MessageForm);
