import React from 'react';
import s from "./Message.module.scss";

export default ({id, author, message}) => (
    <div className={author === 'Me' ? s.myMessage: s.message} key={id}>
        <div className={s.author}>
            {author}
        </div>
        <div className={s.text}>{message}</div>
    </div>
);
